import React,{useState} from 'react'
import './components/App.css';


const App=()=> {
  const [result, setResult] = useState("");
  const handleClick = (e) => {
             setResult(result.concat(e.target.name));
  }
  const clear = ()=>{
         setResult("");
  }

  const backspace = () =>{
      setResult(result.slice(0,-1));
  }
  const calculate = () =>{
    try{
      //eslint-disable-next-line
      setResult(eval(result).toString());

    } catch(err)
    {
      setResult("Error")
    }
  }
  return (
    <>
      <nav className="navbar">
                    <div className="container-fluid">
                        <a className="navbar-brand" href="/">Ezz Calculator...</a>
                    </div>
      </nav>
      <div className="container">
        <form>
           <input type="text" value={result}/>
        </form>
        <div className="buttons">
          <button className="standout" onClick={clear} id="clear">Clear</button>
          <button className="standout" onClick={backspace} id="backspace">C</button>
          <button className="standout" name="/" onClick={handleClick}>&divide;</button>
          <button name="7" onClick={handleClick}>7</button>
          <button name="8" onClick={handleClick}>8</button>
          <button name="9" onClick={handleClick}>9</button>
          <button className="standout" name="*" onClick={handleClick}>&times;</button>
          <button name="4" onClick={handleClick}>4</button>
          <button name="5" onClick={handleClick}>5</button>
          <button name="6" onClick={handleClick}>6</button>
          <button className="standout" name="-" onClick={handleClick}>&ndash;</button>
          <button name="1" onClick={handleClick}>1</button>
          <button name="2" onClick={handleClick}>2</button>
          <button name="3" onClick={handleClick}>3</button>
          <button className="standout" name="+" onClick={handleClick}>+</button>
          <button name="0" onClick={handleClick}>0</button>
          <button name="." onClick={handleClick}>.</button>
          <button className="standout" onClick={calculate} id="equalto">=</button>
        </div>
      </div>
    </>
  )
}

export default App;